@use "." as this;
@use "@pkg/react-storybook/Component";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Style/Shadow";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Display";
@use "@app/bancor-components/Style/Layout/Padding";

#{this.$selector} {
    header {
        .title {
            @include Display.set($display: flex);
        }
        & #{Text.$selector} {
            @include Font.set($size: iota, $weight: bold);
            @include Margin.set($bottom: alpha);
            word-break: break-word;
        }
        & #{Image.$selector} {
            @include Image.asModoLogo;
        }
    }

    section {
        .actions {
            @include Flex.set($justify: end);
        }

        .responseIcon {
            @include Flex.set($justify: center);
            @include Margin.set($top: beta, $bottom: alpha);
        }

        .responseIcon:last-child > svg {
            width: Layout.get-size(lambda);
        }

        .responseTitle#{Text.$selector} {
            @include Font.set($size: delta, $weight: bold, $align: center);
        }

        .responseDescription#{Text.$selector} {
            @include Font.set($size: delta, $align: center);
            @include Margin.set($bottom: gamma);
        }
    }

    #{Button.$selector}.navigateButton {
        @include Display.set($display: flex);
        @include Button.asUnderlineLink;
        @include Margin.set($bottom: none, $top: none, $left: auto, $right: auto);
    }

    footer {
        #{Button.$selector} {
            @include Margin.set($top: beta, $bottom: alpha);
        }
    }
}
